import React, { useState, useEffect } from 'react';
import Place from './Place';
import { CircularProgress, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { getGifUrl } from '../../api/api';

const useStyles = makeStyles((theme) => ({
  loading: {
    height: '200px', display: 'flex', justifyContent: 'center', alignItems: 'center',
  },
  resultsCount: {
    position: 'absolute', right: 20, top: 10, color: 'gray', fontSize: 12,
  }
}));


const hideAppBar = () => {
  const appbar = document.getElementsByClassName('appbar')[0];
  const placesList = document.getElementsByClassName('place-holder-seperator')[0];
  const scrolledUp = placesList.getBoundingClientRect().top < 200;
  appbar.style.opacity = scrolledUp ? 0 : 1;
};

const NothingFoundView = () => (
  <>
    <Typography variant="h5" align='center' style={{ marginTop: '40px', display: 'block' }}>
      Nothing Found üòî
    </Typography>
    <Typography variant="body1" align='center' style={{ marginTop: '20px', display: 'block', color: 'gray' }}>
      Try searching a different time or removing a filter.
    </Typography>
  </>
);

const LoadingView = ({ className, gifUrl }) => (
  <div>
    <Typography variant="h5" flex='12' align='center' style={{ marginTop: '40px' }}>
      Loading Happiness üòÄ
    </Typography>
    <div className={className}>
      {gifUrl ?
        (<img src={gifUrl} alt={'happy gif'} style={{ marginTop: '20px' }} />) :
        (<CircularProgress size="3rem" />)
      }
    </div>
  </div>
);

const PlaceList = ({ places, isLoading, ...props }) => {
  const classes = useStyles();
  const [gifUrl, setGifUrl] = useState(null);
  useEffect(() => {
    if (!isLoading) {
      // fetch new gif before we need to show it
      getGifUrl().then(setGifUrl);
    }
  }, [isLoading]);


  return (
    <div class="places-list" onScroll={hideAppBar}>
      <div class="place-holder"></div>
      <div class="place-holder-seperator">
        {places.length > 0 && !isLoading &&
          <span className={classes.resultsCount}>{places.length} Results</span>
        }
        <div class="tiny-gray-box"></div>
      </div>
      <div class="places-list-container">
        {isLoading ? (
          <LoadingView className={classes.loading} gifUrl={gifUrl} />
        ) : (
          places.length > 0 ? places.map((place, i) => (
            <Place
              place={place}
              timeFilter={props.timeFilter}
              dealsFilter={props.dealsFilter}
              key={i}
            />
          )) : <NothingFoundView />
        )}
      </div>
    </div>
  );
}
export default PlaceList;
