/* eslint-disable consistent-return */
import axios from 'axios';
import ReactGA from 'react-ga';
import { day, time, formatTimeCondense } from '../utils';


const handleTimeFilter = (timeFilter) =>
  timeFilter.toLowerCase() === 'now' ? `now/${day}%20${time}` : `now/${day}%20${formatTimeCondense(timeFilter)}`;


export const getPlacesData = async (timeFilter) => {
  try {
    const { data } = await axios.get(`https://gbjt9fglc8.execute-api.us-west-2.amazonaws.com/api/places/${handleTimeFilter(timeFilter)}`);
    return data;
  } catch (error) {
    console.log(error);
    ReactGA.exception({
      description: 'Error fetching data',
      fatal: true
    });
    return [];
  }
};

const GIPHY_API_KEY = 'ETZSGjtbo4adXd6ndqDKP2NnbmmPrSnc';

const defaultGifUrl = 'https://media4.giphy.com/media/MeIucAjPKoA120R7sN/200_d.gif?cid=a02e69abh006ugw9bfr38wkz45qsyywvglh4cczq8k1rj4ts&rid=200_d.gif&ct=g';

export const getGifUrl = async (keyword = 'excited') => {
  const url = `https://api.giphy.com/v1/gifs/random?api_key=${GIPHY_API_KEY}&tag=${keyword}&limit=1`;
  try {
    const { data: { data } } = await axios.get(url);
    return data.fixed_height_downsampled_url;
  } catch (error) {
    return defaultGifUrl;
  }
};
