const datetime = new Date();
const dayArray = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
export const day = dayArray[datetime.getDay()];

function formatAMPM(date) {
  var hours = date.getHours();
  var minutes = date.getMinutes();
  var ampm = hours >= 12 ? 'P' : 'A';
  hours = hours % 12;
  hours = hours ? hours : 12; // the hour '0' should be '12'
  minutes = minutes < 10 ? '0' + minutes : minutes;
  var strTime = hours + ':' + minutes + '' + ampm;
  return strTime;
}

export const time = formatAMPM(datetime);

const dayMap = { 'Monday': 1, 'Tuesday': 2, 'Wednesday': 3, 'Thursday': 4, 'Friday': 5, 'Saturday': 6, 'Sunday': 7 };

export const getTodaysTime = (times) => {
  const todays_time = times.filter(time =>
    dayMap[time.start_day] <= dayMap[day]
    && dayMap[time.end_day] >= dayMap[day]
  );
  return todays_time.length > 0 ? todays_time[0] : {};
}

export const formatTimeExpand = (time) => time.replace('P', ' PM').replace('A', ' AM')

export const formatTimeCondense = (time) => time.replace(' PM', 'P').replace(' AM', 'A')

export const doIntersect = (a, b) => a.filter(x => b.includes(x)).length > 0;

export const singularize = (str) => str.endsWith('s') ? str.substr(0, str.length - 1) : str;
